*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.add-note{
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 4%;
    bottom: 5%;
    font-size: 3.5rem;
    color: #0b5ed7;
    background-color: transparent;
    border: none;
    padding: 0;
    transition: 0.3s;
    z-index: 10;
}
.add-note:hover{
    opacity: 0.6;
}
.note-box{
    position: relative;
}
.note-box:hover{
    cursor: pointer;
}
.note-box:hover .note-icon{
    opacity: 1;
}
.note-icon{
    width: fit-content;
    background-color: transparent;
    color: white;
    border: none;
    position: absolute;
    top: 5%;
    opacity: 0;
    transition: 0.3s;
}
.note-icon:nth-child(1){
    left: 2%;
}
.note-icon:nth-child(2){
    right: 2%;
}


